import fetch from 'isomorphic-unfetch';
import { useState, useEffect } from 'react';
import Dashboard from '../components/Dashboard/Dashboard';

const Listings = ({users})=>{
    let userListings, othersListings;
    const [listingToRender, changeListing] = useState([]);
    const [toggleStatus, toggle] = useState('userListings');
    const [testUser] = useState('abgfigu143668');

    useEffect(()=>{
        for (const user in users) {
            if(user === testUser){
                userListings = listings[testUser].listings;
                changeListing(userListings);
            }else{
                othersListings = listings[user].listings;
                changeListing(othersListings);
            };
        }
    });

    console.log(listings);
    
    return <Dashboard pageTitle="Listings - EazyRent" route="listings" toggle={toggle} toggleStatus={toggleStatus} listings={listingToRender} user={testUser}/>;
}

Listings.getInitialProps = async()=>{
    const res = await fetch('http://localhost:3004/users');
    const data = await res.json();

    return {
        users: data
    }
}

export default Listings;